@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/Lato-Regular.woff2") format("woff2"), url("../fonts/Lato-Regular.woff") format("woff");
}
*,
::after,
::before {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  position: relative;
  display: grid;
  width: 100%;
  min-width: 320px;
  min-height: 100vh;
  margin: 0 auto;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
}

img {
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}

.container {
  position: relative;
  width: 100%;
  min-width: 320px;
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0);
}

.button {
  display: block;
  width: 168px;
  margin: 0 auto;
  padding: 10px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}
.button:hover, .button:focus {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5eaa2f;
}

@media (min-width: 1440px) {
  .header {
    position: relative;
  }
}

@media (min-width: 1440px) {
  .header--index::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 50%;
    z-index: -1;
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/promo-bg-desktop.jpg");
    background-repeat: no-repeat;
    background-position: left;
    background-size: cover;
  }
}

.header__container {
  padding: 0;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 191px;
  height: 38px;
  margin-left: 19px;
}
@media (min-width: 768px) {
  .logo {
    width: 174px;
    height: 52px;
    margin-left: 0;
  }
}
@media (min-width: 1440px) {
  .logo {
    width: 202px;
    height: 59px;
  }
}
.logo:hover, .logo:focus {
  opacity: 0.8;
}
.logo:active {
  opacity: 0.6;
}

.logo img {
  display: block;
  width: 100%;
  height: 100%;
}

.menu {
  position: relative;
  min-height: 65px;
  padding-top: 10px;
}
@media (min-width: 768px) {
  .menu {
    display: grid;
    grid-template-columns: repeat(2, auto);
    gap: 20px;
    align-items: center;
    padding: 2px 30px 20px;
  }
}
@media (min-width: 1440px) {
  .menu {
    grid-template-columns: repeat(2, 1fr);
    padding: 52px 0 20px;
  }
}

.menu__btn {
  position: absolute;
  top: 23px;
  right: 20px;
  width: 24px;
  height: 18px;
  background: none;
  border: none;
  border-top: 2px solid #000000;
}
@media (min-width: 768px) {
  .menu__btn {
    display: none;
  }
}
.menu__btn::before, .menu__btn::after {
  content: "";
  position: absolute;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.menu__btn::before {
  top: 45%;
  transform: translateY(-50%);
}
.menu__btn::after {
  bottom: 0;
}

.menu--open .menu__btn {
  border-top: 2px solid transparent;
}
.menu--open .menu__btn::before {
  transform: translateY(-50%) rotate(-46deg);
}
.menu--open .menu__btn::after {
  top: 45%;
  transform: translateY(-50%) rotate(46deg);
}

.menu__content-list {
  position: absolute;
  top: 65px;
  z-index: 10;
  display: none;
  flex-direction: column;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .menu__content-list {
    position: static;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    background-color: transparent;
  }
}

.menu--open .menu__content-list {
  display: flex;
}

.menu__content-item {
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .menu__content-item {
    margin-left: 36px;
    border: none;
  }
}
@media (min-width: 1440px) {
  .menu__content-item {
    margin-left: 35px;
  }
}

.menu__content-link {
  display: block;
  padding-top: 23px;
  padding-bottom: 22px;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 768px) {
  .menu__content-link {
    position: relative;
    padding: 10px 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .menu__content-link {
    padding: 6px 0;
    font-size: 20px;
    line-height: 30px;
  }
}
.menu__content-link:hover, .menu__content-link:focus {
  opacity: 0.6;
}
.menu__content-link:active {
  opacity: 0.3;
}

@media (min-width: 1440px) {
  .header--index .menu__content-link {
    color: #ffffff;
  }
}

@media (min-width: 768px) {
  .menu__content-link--current::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 2px;
    background-color: #68b738;
  }
}

@media (min-width: 1440px) {
  .header--index .menu__content-link--current::before {
    background-color: #ffffff;
  }
}

@media (max-width: 767px) {
  .no-js .menu__btn {
    display: none;
  }
}

@media (max-width: 767px) {
  .no-js .menu__content-list {
    position: static;
    display: flex;
  }
}

@media (max-width: 767px) {
  .no-js .menu__logo {
    margin-bottom: 10px;
  }
}

