.control {
  position: relative;
  display: block;
}

.control__mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
}

.control:hover .control__mark {
  border-color: #bababa;
}

.control__field:disabled + .control__mark {
  opacity: 0.3;
}

.control__field[type=radio] + .control__mark {
  border-radius: 50%;
}

.control__field[type=radio]:checked + .control__mark::before {
  position: absolute;
  top: 8px;
  left: 8px;
  width: 13px;
  height: 13px;
  background-color: #68b738;
  border-radius: 50%;
  content: "";
}

.control__label {
  line-height: 30px;
}

@media (min-width: 768px) {
  .control__mark {
    position: absolute;
    top: -5px;
    left: 0;
    width: 40px;
    height: 40px;
    border: 1px solid #cdcdcd;
  }

  .control__label {
    font-size: 20px;
    line-height: 150%;
  }

  .control__field[type=radio]:checked + .control__mark::before {
    position: absolute;
    top: 9px;
    left: 12px;
    width: 18px;
    height: 18px;
    background-color: #68b738;
    border-radius: 50%;
    content: "";
  }
}
.checkbox {
  position: relative;
  display: block;
}

.checkbox__mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
}

.checkbox:hover .checkbox__mark {
  border-color: #bababa;
}

.checkbox__field:disabled + .checkbox__mark {
  opacity: 0.3;
}

.checkbox__field[type=checkbox]:checked + .checkbox__mark::before {
  position: absolute;
  top: 13px;
  left: 4px;
  width: 10px;
  height: 2px;
  background-color: #68b738;
  content: "";
  transform: rotate(45deg);
}

.checkbox__field[type=checkbox]:checked + .checkbox__mark::after {
  position: absolute;
  top: 12px;
  right: 4px;
  width: 14px;
  height: 2px;
  background-color: #68b738;
  content: "";
  transform: rotate(-45deg);
}

@media (min-width: 768px) {
  .checkbox__mark {
    position: absolute;
    top: 0;
    left: 0;
    width: 40px;
    height: 40px;
    border: 1px solid #cdcdcd;
  }

  .checkbox__field[type=checkbox]:checked + .checkbox__mark::before {
    position: absolute;
    top: 20px;
    left: 7px;
    width: 12px;
    height: 2px;
    background-color: #68b738;
    content: "";
    transform: rotate(45deg);
  }

  .checkbox__field[type=checkbox]:checked + .checkbox__mark::after {
    position: absolute;
    top: 18px;
    right: 6px;
    width: 18px;
    height: 2px;
    background-color: #68b738;
    content: "";
    transform: rotate(-45deg);
  }
}